import React from "react";
import { Card, Box } from "@mui/material";
import { customCardProps } from "../types/types";

const CustomCard = React.memo(function CustomCard({
  children,
  title,
  titleIcon,
  badgeColor = "#ffff",
  badgePosition = "top-right",
  sx = {},
  badgeStyle,
  ...rest
}: customCardProps & {
  badgeColor?: string;
  badgePosition?: 'top-left' | 'top-right';
  sx?: any;
  titleIcon?: React.ReactNode;
  badgeStyle?: React.CSSProperties;

}) {
  // Badge position styles
  const badgeStyles = badgePosition === 'top-left'
    ? { top: 18, left: 18, right: 'auto' }
    : { top: -10, right: 22, left: 'auto' };

  return (
   <Box sx={{ position: "relative", mb: 1, width: "100%" }}>
  <Card
    sx={{
      background: '#ffff',
      // border: '4px solid white',
      borderRadius: "20px",
      // boxShadow: "0 4px 24px 0 rgba(60,72,128,0.07)",
      boxShadow: 'none',
      minHeight: 220,
      position: "relative",
      overflow: "visible",
      p: 0,
      color: 'inherit',
      ...sx //  THIS will override background and borderRadius
    }}
    {...rest}
  >
    {/* Badge */}
    {title && (
      <Box
        sx={{
          position: "absolute",
          ...badgeStyles,
          ...(badgeStyle || {}),
          background: badgeStyle?.backgroundColor || badgeColor,
          color: "#495052",
          px: 2,
          py: 0.5,
          borderRadius: "12px",
          fontSize: "0.85rem",
          fontWeight: 500,
          zIndex: 2,
          // boxShadow: "0 2px 8px 0 rgba(78, 86, 125, 0.1)",
        }}
      >
        {titleIcon}
        {title}
      </Box>
    )}

    {/* Content */}
    <Box sx={{ width: '100%', p: 2, color: 'inherit' }}>
      {children}
    </Box>
  </Card>
</Box>

  );
});

export default CustomCard;

