import { Box, Grid, Card, Typography, Button } from "@mui/material";
import React, { useEffect, useState } from "react";
import BookedSlots from "./dashboard/bookedSlots";
import { CardPage, Checklist } from "./dashboard/cardPage";
import Events from "./dashboard/events";
import Advisors from "./dashboard/advisors";
import Images from "src/assets";

const carouselData = [
  {
    title: "Exclusive Scholarships",
    subtitle: "Limited-Time Scholarship Finder",
    description: "Explore scholarships tailored to your profile, limited-time access available now!",
    button: "Unlock now",
    image: Images.MoneyBag,
    bg: "#B7A9F9"
  },
  {
    title: "Free SOP review",
    subtitle: "Get expert feedback on your SOP.",
    description: "Improve its clarity, structure, and overall impact before you submit.",
    button: "Improve my sop",
    image: Images.SopDashboard, 
    bg: "#6DB1B3"
  },
  {
    title: "Free test prep access",
    subtitle: "Test smarter-practice for free.",
    description: "practice with mock tests, tips, and guides to ace your exams.",
    button: "Try for free",
    image: Images.TestDashboard,
    bg: "#EE6F57 "
  },
  {
    title: "Profile review",
    subtitle: "Get a free expert review.",
    description: "Boost your chances of admission with a free expert.",
    button: "Get free review",
    image: Images.ProfileDashboard,
    bg: "#F2A05E"
  }
];

const ScholarshipCarousel = () => {
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const timer = setTimeout(() => {
      setIndex((prev) => (prev + 1) % carouselData.length);
    }, 2000);
    return () => clearTimeout(timer);
  }, [index]);

  const card = carouselData[index];

  return (
    <>
      <Card
        sx={{
          background: card.bg,
          borderRadius: '32px',
          mt: 4,
          p: 4,
          color: '#fff',
          display: 'flex',
          alignItems: 'flex-start',
          minHeight: 160,
          boxShadow: 0,
          position: 'relative',
          overflow: 'visible',
        }}
      >
        <Box sx={{ flex: 1, pr: 2 }}>
          <Typography fontWeight={500} fontSize="1.1rem" mb={1} sx={{ color: '#fff', textAlign: 'left' }}>
            {card.title}
          </Typography>
          <Typography fontWeight={300} fontSize="1.0rem" mb={1} sx={{ color: '#fff', textAlign: 'left' }}>
            {card.subtitle}
          </Typography>
          <Typography fontSize="0.8rem" mb={2} sx={{ color: '#fff', textAlign: 'left' }}>
            {card.description}
          </Typography>
          <Button
            variant="contained"
            sx={{
              background: '#fff',
              color: '#222',
              borderRadius: '24px',
              fontWeight: 700,
              px: 3,
              py: 1,
              textTransform: 'none',
              boxShadow: '0 2px 8px 0 rgba(0,0,0,0.10)',
              minWidth: 120,
              '&:hover': { background: '#f5f5f5', color: '#222' },
            }}
          >
            {card.button}
          </Button>
        </Box>
        <Box sx={{ position: 'absolute', right: 32, bottom: 0 }}>
          <img
            src={card.image}
            alt={card.title}
            style={{ width: 120, height: 'auto', borderRadius: 0 }}
          />
        </Box>
      </Card>
      {/* Carousel dots */}
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 1, mb: 2 }}>
        {carouselData.map((_, i) => (
          <Box
            key={i}
            sx={{
              width: 10,
              height: 10,
              borderRadius: '50%',
              background: i === index ? '#A993FF' : '#F2EFFF',
              mx: 0.5
            }}
          />
        ))}
      </Box>
    </>
  );
};

const Dashboard = () => {
  return (
    <Box>
      {/* Activity and Recommendations sections (CardPage) at the top */}
      <Grid container spacing={1.5}>
        <Grid item xs={12} md={7}>
          <CardPage />
        </Grid>
        <Grid item xs={12} md={5}>
          <ScholarshipCarousel/>
        </Grid>
      </Grid>
      {/* Advisors, Upcoming Meetings, Pending Task row below Recommendations */}
      <Grid container spacing={1.5}>
        <Grid item xs={12} md={7} mt={2}>
          <Grid container spacing={1}>
            <Grid item xs={12} md={5}>
              <Advisors />
            </Grid>
            <Grid item xs={12} md={7}>
              <BookedSlots />
              <Checklist />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} md={5}>
          <Events />
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;
