import {
    Box,
    Button,
    Dialog,
    DialogContent,
    DialogTitle,
    FormControl,
    InputLabel,
    MenuItem,
    Select,
    TextField,
    Typography,
    IconButton,
    Autocomplete,
    Chip
  } from "@mui/material";
  import CloseIcon from "@mui/icons-material/Close";
  import React, { useEffect, useState } from "react";
  import { useNavigate } from "react-router-dom";
  import { useAppDispatch, useAppSelector } from "src/assets/hooks";
import { academicTests, gradeScaleOptions, languageTests } from "src/assets/constants/misc";
import { editProfile, getRecommendations } from "src/services";
import { setPopup } from "src/store/Slices/popupSlice";
import { countryList } from "src/assets/menu";
import { setRecommendEditData } from 'src/store/Slices/recommendEditSlice';
import { courseList } from "src/assets/constants/courseList";
import { Test } from "src/types/types";
import { setRecommended } from "src/store/Slices/recommendations";

  
  interface Criterion {
    label: string;
    data: {
      value: any;
      editLink?: string;
    };
  }
  
  interface RecommendationEditModalProps {
    open: boolean;
    onClose: () => void;
    data: any[];
    onSubmit?: (updatedData: Record<string, string>) => void;
    //courseList: string[]; 
  }

  
  const testOptions = [
    { label: "IELTS", value: "IELTS" },
    { label: "TOEFL", value: "TOEFL" },
    { label: "Duolingo", value: "Duolingo" },
    { label: "PTE", value: "PTE" },
  ];
  
  const testOptions1 = [
    { label: "GRE", value: "GRE" },
    { label: "GMAT", value: "GMAT" },
    { label: "SAT", value: "SAT" },
    { label: "ACT", value: "ACT" },
  ];
  
  const outOff=[
    {label:'Percentage out of 100',value:100},
    {label:'CGPA out of 10',value:10},
    {label:"CGPA out of 7",value:7},
    {label:"CGPA out of 5",value:5},
    {label:"CGPA out of 4",value:4}
  ]
  
  const RecommendationEditModal = ({
    open,
    onClose,
    data,
    onSubmit,
  }: RecommendationEditModalProps) => {
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const recommendations = useAppSelector((state) => state.recommendations);
    const recommendEditData = useAppSelector((state) => state.recommendEdit);
    const criteria: Criterion[] = recommendations?.data?.criteria || [];
    const profile = useAppSelector((state) => state.profile);
    const [formData, setFormData] = useState<Record<string, any>>({});
    

   useEffect(() => {
    if (criteria.length) {
      const gpaData = criteria.find((item) => item.label === "GPA")?.data.value;
      const [score, maxScore] = gpaData ? gpaData.split("/").map((s: string) => s.trim()) : ['', ''];
      let temp = {
        studyLevel: criteria.find((item) => item.label === "StudyLevel")?.data.value || 'Bachelors',
        bachelorsScore: score,
        gradeScale: gradeScaleOptions.find((item) => item.maxScore.toString() === maxScore)?.label || '',
        backlogs: criteria.find((item) => item.label === "Backlogs")?.data.value || '',
        englishTestType: languageTests.find((item) => criteria.find((item2) => item2.label === item.fullForm))?.shortForm || '',
        englishTestScore: criteria.find((item) => languageTests.find((item2) => item2.fullForm === item.label))?.data.value || '',
        academicTestType: academicTests.find((item) => criteria.find((item2) => item2.label === item.fullForm))?.shortForm || '',
        academicTestScore: criteria.find((item) => academicTests.find((item2) => item2.fullForm === item.label))?.data.value || '',
        country: criteria.find((item) => item.label === "Country")?.data.value || [],
        subCategory: criteria.find((item) => item.label === "SubCategory")?.data.value?.map((item: string) => ({ label: item, value: item })) || []
      };
      setFormData(temp);
      // Store in Redux
      dispatch(setRecommendEditData(temp));
    }
  }, [criteria, dispatch]);

  
    const handleChange = (field: string, value: any) => {
      const newData = {
        ...formData,
        [field]: value,
      };
      setFormData(newData);
      // Update Redux store with the entire form data to maintain consistency
      dispatch(setRecommendEditData(newData));
    };

    const validateScores = () => {
      let error = '';
      // Academic Test Validation
      const selectedTest = testOptions1.find(opt => opt.label === formData["academicTestType"]);
      const academicScore = Number(formData["academicTestScore"]);
      if (selectedTest?.label === "GRE") {
        if (academicScore < 260 || academicScore > 340 || academicScore % 1 !== 0) {
          error = "GRE score must be between 260 and 340 ";
        }
      } else if (selectedTest?.label === "GMAT") {
        if (academicScore < 200 || academicScore > 800 || academicScore % 5 !== 0) {
          error = "GMAT score must be between 200 and 800";
        }
      } else if (selectedTest?.label === "SAT") {
        if (academicScore < 200 || academicScore > 1600 || academicScore % 1 !== 0) {
          error = "SAT score must be between 200 and 1600";
        }
      } else if (selectedTest?.label === "ACT") {
        if (academicScore < 1 || academicScore > 36 || academicScore % 1 !== 0) {
          error = "ACT score must be between 1 and 36 ";
        }
      }
      // English Test Validation
      const selectedEngTest = testOptions.find(opt => opt.label === formData["englishTestType"]);
      const englishScore = Number(formData["englishTestScore"]);
      if (selectedEngTest?.label === "IELTS") {
        const isHalfStep = Math.abs(englishScore * 10) % 5 === 0;
        if (englishScore < 0 || englishScore > 9 || !isHalfStep) {
          error = "IELTS score must be between 0 and 9 ";
        }
      } else if (selectedEngTest?.label === "TOEFL") {
        if (englishScore < 0 || englishScore > 120 || englishScore % 1 !== 0) {
          error = "TOEFL score must be between 0 and 120";
        }
      } else if (selectedEngTest?.label === "Duolingo") {
        if (englishScore < 0 || englishScore > 160 || englishScore % 1 !== 0) {
          error = "Duolingo score must be between 0 and 160";
        }
      } else if (selectedEngTest?.label === "PTE") {
        if (englishScore < 0 || englishScore > 90 || englishScore % 1 !== 0) {
          error = "PTE score must be between 0 and 90";
        }
      }
      // Grade Scale Validation
      const gradeScale = formData["gradeScale"];
      const bachelorsScore = Number(formData["bachelorsScore"]);
      if (gradeScale === "Percentage out of 100") {
        if (bachelorsScore < 0 || bachelorsScore > 100) {
          error = "Percentage must be between 0 and 100";
        }
      } else if (gradeScale === "CGPA out of 10") {
        if (bachelorsScore < 0 || bachelorsScore > 10) {
          error = "CGPA out of 10 must be between 0 and 10";
        }
      } else if (gradeScale === "CGPA out of 7") {
        if (bachelorsScore < 0 || bachelorsScore > 7) {
          error = "CGPA out of 7 must be between 0 and 7";
        }
      } else if (gradeScale === "CGPA out of 5") {
        if (bachelorsScore < 0 || bachelorsScore > 5) {
          error = "CGPA out of 5 must be between 0 and 5";
        }
      } else if (gradeScale === "CGPA out of 4") {
        if (bachelorsScore < 0 || bachelorsScore > 4) {
          error = "CGPA out of 4 must be between 0 and 4";
        }
      }
      return error;
    };

    const submit=async ()=>{
      const validationError = validateScores();
      if (validationError) {
        dispatch(setPopup({
          show: true,
          data: {
            container: {
              name: "error",
              data: validationError,
              dimensions: { width: "30%", height: "40%", show: "none" },
            },
            type: "custom",
          },
        }));
        return;
      }
      if(profile.data)
      {
        let profileTests=[...profile.data?.tests??[]];
      let newTests=[
        {
          name:languageTests.find((item)=>item.shortForm===formData["englishTestType"])?.fullForm,
          type:"language",
          scores:[
            {
              description:"Total",
              count:formData["englishTestScore"]
            }
          ]
        },
        {
          name:academicTests.find((item)=>item.shortForm===formData["academicTestType"])?.fullForm,
          type:'academic',
          scores:[
            {
              description:"Total",
              count:formData["academicTestScore"]
            }
          ]
        }
      ]
      newTests.forEach((tst)=>{
        let current=profileTests.findIndex((item)=>item.name===tst.name)
        if(current!==-1)
        {
          
          profileTests[current]={...profileTests[current],scores:[...profileTests[current].scores.filter((item)=>item.description!=="Total"),...tst.scores]};
        }
        else{
          let clearedProfile=profileTests.filter((item)=>(tst.type==="language"?languageTests:academicTests).find((item2)=>item2.fullForm===item.name)!==undefined?false:true);
          clearedProfile.push({
            name:tst.name ?? "",
            scores:tst.scores
          } as any);
          profileTests=clearedProfile;
        }
      })
        const temp={
          tests:profileTests,
          education:{...profile.data.education,
            underGraduation:{...profile.data.education.underGraduation,
              totalScore:parseInt(formData["bachelorsScore"]),
              backlogs:parseInt(formData["backlogs"]),
              maxScore:gradeScaleOptions.find((item)=>item.label===formData["gradeScale"])?.maxScore,
              gradingSystem:gradeScaleOptions.find((item)=>item.label===formData["gradeScale"])?.value
            }},
          preference:{...profile.data.preference,
            subCategory:formData["subCategory"].map((item:{label:string,value:string})=>item.value),
            country:formData["country"]
          }
        };
        // setLoader(true)
        const response = await editProfile(temp);
        if (response) {
          
          // Regenerate recommendations with updated criteria
          try {
            // Send the updated profile data to get fresh recommendations
            const recommendationsResponse = await getRecommendations(temp);
            
            if (recommendationsResponse && recommendationsResponse.data) {
              dispatch(setRecommended(recommendationsResponse.data));
              
              // Close the modal first
              onClose();
              
              // Show success message
              dispatch(
                setPopup({
                  show: true,
                  data: {
                    container: {
                      name: "success",
                      data: "Criteria Updated Successfully! Recommendations have been regenerated with your new preferences.",
                      dimensions: {
                        width: "35%",
                        height: "40%",
                        show: "none"
                      },
                    },
                    type: "custom",
                  },
                })
              );
            } else {
              console.error("No recommendations data received");
              throw new Error("No recommendations data received");
            }
          } catch (error) {
            console.error("Failed to regenerate recommendations:", error);
            
            // Close the modal first
            onClose();
            
            // Show error message
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "error",
                    data: "Profile updated successfully, but failed to regenerate recommendations. Please try again later.",
                    dimensions: {
                      width: "35%",
                      height: "40%",
                      show: "none"
                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } else {
          console.error("Failed to update profile");
          dispatch(
            setPopup({
              show: true,
              data: {
                container: {
                  name: "error",
                  data: "Failed to update profile. Please try again.",
                  dimensions: {
                    width: "35%",
                    height: "40%",
                    show: "none"
                  },
                },
                type: "custom",
              },
            })
          );
        }
        //setLoader(false)
      }
    }

  
    return (
      <Dialog open={open} onClose={onClose} fullWidth maxWidth="sm">
        <DialogTitle sx={{ 
          fontWeight: 600, 
          fontSize: "1.2rem", 
          pr: 4,
          borderBottom: "1px solid #eee",
          pb: 2
        }}>
          Your recommendations are based on:
          <IconButton
            onClick={onClose}
            sx={{ position: "absolute", top: 12, right: 12 }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
  
        <DialogContent sx={{ mt: 2 }}>
          <Box display="flex" flexDirection="column" gap={3}>
            {/* Bachelors Score Section */}
            <Box>
              <Typography variant="subtitle1" fontWeight={500} gutterBottom>
                {formData["studyLevel"] || 'Bachelors'}
              </Typography>
              <Box display="flex" gap={2}>
                <TextField
                  size="small"
                  label="Enter Score"
                  value={formData["bachelorsScore"] || ""}
                  onChange={(e) => handleChange("bachelorsScore", e.target.value)}
                  sx={{ flex: 1 }}
                />
                <FormControl size="small" sx={{ flex: 1 }}>
                  <InputLabel>Grade scale</InputLabel>
                  <Select
                    label="Grade scale"
                    value={formData["gradeScale"] || ""}
                    onChange={(e) => handleChange("gradeScale", e.target.value)}
                  >
                    {outOff.map((item) => (
                      <MenuItem key={item.value} value={item.label}>
                        {item.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <TextField
                  size="small"
                  label="Backlogs"
                  value={formData["backlogs"] || ""}
                  onChange={(e) => handleChange("backlogs", e.target.value)}
                  sx={{ flex: 1 }}
                />
              </Box>
            </Box>

            {/* English Test Section */}
            <Box>
              <Typography variant="subtitle1" fontWeight={500} gutterBottom>
                English proficiency test
              </Typography>
              <Box display="flex" gap={2}>
                <FormControl size="small" sx={{ flex: 1 }}>
                  <InputLabel>Select test</InputLabel>
                  <Select
                    label="Select test type"
                    value={formData["englishTestType"] || ""}
                    onChange={(e) => handleChange("englishTestType", e.target.value)}
                  >
                    {testOptions.map((option) => (
                      <MenuItem key={option.value} value={option.label}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <TextField
                  size="small"
                  label="Enter score"
                  value={formData["englishTestScore"] || ""}
                  onChange={(e) => handleChange("englishTestScore", e.target.value)}
                  sx={{ flex: 1 }}
                />
              </Box>
            </Box>

            {/* GRE/GMAT Section */}
            <Box>
              <Typography variant="subtitle1" fontWeight={500} gutterBottom>
                GRE, GMAT, SAT, ACT
              </Typography>
              <Box display="flex" gap={2}>
                <FormControl size="small" sx={{ flex: 1 }}>
                  <InputLabel>Select test type</InputLabel>
                  <Select
                    label="Select test type"
                    value={formData["academicTestType"] || ""}
                    onChange={(e) => handleChange("academicTestType", e.target.value)}
                  >
                    {testOptions1.map((option) => (
                      <MenuItem key={option.value} value={option.label}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <TextField
                  size="small"
                  label="Enter score"
                  value={formData["academicTestScore"] || ""}
                  onChange={(e) => handleChange("academicTestScore", e.target.value)}
                  sx={{ flex: 1 }}
                />
              </Box>
            </Box>

            {/* Country Selection */}
            <Box>
              <Typography variant="subtitle1" fontWeight={500} gutterBottom>
                Select Country
              </Typography>
              <Autocomplete
                multiple
                size="small"
                options={countryList}
                getOptionLabel={(option) => option}
                value={formData["country"] || []}
                onChange={(event, value) => handleChange("country", value)}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    placeholder="Search countries"
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        padding: '4px 8px'
                      }
                    }}
                  />
                )}
                renderTags={(value, getTagProps) =>
                  value.map((option, index) => (
                    <Chip
                      {...getTagProps({ index })}
                      label={option}
                      size="small"
                      sx={{
                        bgcolor: 'primary.light',
                        color: 'white',
                        '& .MuiChip-deleteIcon': {
                          color: 'white'
                        }
                      }}
                    />
                  ))
                }
                ListboxProps={{
                  style: { maxHeight: 200 }
                }}
              />
            </Box>

            {/* Program Selection */}
            <Box>
              <Typography variant="subtitle1" fontWeight={500} gutterBottom>
                What program would you like to pursue?
              </Typography>
              <Autocomplete
                multiple
                size="small"
                options={courseList}
                getOptionLabel={(option) => option.label}
                value={formData["subCategory"] || []}
                onChange={(event, value) => handleChange("subCategory", value)}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    placeholder="Search programs"
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        padding: '4px 8px'
                      }
                    }}
                  />
                )}
                renderTags={(value, getTagProps) =>
                  value.map((option, index) => (
                    <Chip
                      {...getTagProps({ index })}
                      label={option.label}
                      size="small"
                      sx={{
                        bgcolor: 'primary.light',
                        color: 'white',
                        '& .MuiChip-deleteIcon': {
                          color: 'white'
                        }
                      }}
                    />
                  ))
                }
                ListboxProps={{
                  style: { maxHeight: 200 }
                }}
              />
            </Box>

            {/* Action Buttons */}
            <Box sx={{ 
              display: 'flex', 
              flexDirection: 'column', 
              alignItems: 'center',
              gap: 1,
              mt: 2 
            }}>
              <Button
                variant="contained"
                sx={{
                  width: '30%',
                  backgroundColor: "#F48438",
                  color: "#fff",
                  borderRadius: "999px",
                  py: 1.2,
                  fontWeight: 600,
                  textTransform: "none",
                  "&:hover": {
                    backgroundColor: "#F48438",
                  },
                }}
                onClick={() => {
                  onClose();
                  submit();
                }}
              >
                Continue
              </Button>

                                            <Typography variant="body2" sx={{ color: "#555", textAlign: "center" }}>
                 Want to edit further?{" "}
                 <Box
                   component="span"
                   onClick={() => {
                     // Clear the user-profile data from localStorage to start fresh
                     localStorage.removeItem("user-profile");
                     
                     // Navigate to the initial step (Start page)
                     navigate("/startpage");
                   }}
                   sx={{
                     textTransform: "none",
                     fontWeight: 600,
                     color: 'primary.main',
                     cursor: 'pointer',
                     '&:hover': {
                       color: 'primary.dark',
                     },
                   }}
                 >
                   Edit
                 </Box>
               </Typography>
            </Box>
          </Box>
        </DialogContent>
      </Dialog>
    );
  };
  
  export default RecommendationEditModal;
  